import db from '@/lib/db';
import { NextResponse } from 'next/server';

const ADMIN_PASSWORD = 'Admin@123';

export async function POST(request) {
    try {
        const authHeader = request.headers.get('authorization');
        if (!authHeader || !authHeader.startsWith('Bearer ')) {
            return NextResponse.json({ error: 'Unauthorized' }, { status: 401 });
        }

        const { name, student_id, password } = await request.json();

        if (!name || !student_id || !password) {
            return NextResponse.json({ error: 'Missing required fields' }, { status: 400 });
        }

        // Check if student_id already exists
        const existing = db.prepare('SELECT * FROM users WHERE student_id = ?').get(student_id);
        if (existing) {
            return NextResponse.json({ error: 'Roll number already exists' }, { status: 400 });
        }

        // Add member to database
        const result = db.prepare('INSERT INTO users (student_id, name, role, password) VALUES (?, ?, ?, ?)').run(
            student_id,
            name,
            'member',
            password
        );

        const newUser = {
            id: result.lastInsertRowid,
            student_id,
            name,
            role: 'member',
            created_at: new Date().toISOString()
        };

        // Log the access
        db.prepare('INSERT INTO access_logs (user_id, student_id, name, role) VALUES (?, ?, ?, ?)').run(
            newUser.id,
            student_id,
            name,
            'member'
        );

        return NextResponse.json(newUser);
    } catch (error) {
        console.error('Error adding member:', error);
        return NextResponse.json({ error: 'Internal Server Error' }, { status: 500 });
    }
}
